.menu {
    > li {
        a {
            text-align: right;
        }

        &.menu-item-has-children {
            position: relative;

            .sub-menu {
                left: auto;
                right: 0;
            }

        }

        &.has-mega-menu {
            .mega-menu {

                left: auto;
                right: 0;
            }
        }
    }
}

.mega-menu {
    .mega-menu__column {
        h4 {
            text-align: right;
        }
    }
}

.header {
    .menu--product-categories {
        .menu__toggle {
            i {
                margin-left: 1em;
                margin-right: 0;
            }
        }
        .menu--dropdown {
            li {
                > a {
                    text-align: right;
                }
            }
            > li {
                &.menu-item-has-children {
                    .mega-menu {
                        left: auto;
                        right: 100%;
                    }
                }
            }
        }
    }

    .ps-form--quick-search {
        button {
            border-radius: 4px 0 0 4px;
        }

        .form-group--icon {
            //margin-left: 40px;

            i {
                right: auto;
                left: 10px;
            }

            select {
                border-left: 1px solid #ccc;
                border-right: 0;
                border-radius: 0 4px 4px 0 !important;
            }
        }
    }

    .header__actions {
        .ps-block--user-header {
            text-align: right;

            .ps-block__right {
                padding-left: 0;
                padding-right: 10px;
            }
        }
    }

    @media screen and (min-width: 1200px) {
        .header__top {
            .ps-container {
                display: grid;
                grid-template-columns: 300px 1fr 400px;
            }
        }
    }
}

.header--market-place-2, .header--market-place-4 {
    .menu--product-categories {
        .menu__toggle {
            i {
                margin-left: 0;
                margin-right: 0.5em;
            }
        }
    }
}

.ps-footer {
    .ps-footer__links {
        text-align: right;

        a {
            &:after {
                margin-left: 0;
                margin-right: 5px;
            }
        }
    }
}
