#homepage-1 {
    .ps-home-banner {

        .owl-rtl {
            .owl-next {
                i {
                    &:before {
                        content: "\e93b" !important;
                    }
                }

            }

            .owl-prev {
                i {
                    &:before {
                        content: "\e93c" !important;
                    }
                }
            }
        }

        @media screen and (min-width: 1200px) {
            .ps-section__left {
                padding-left: 30px;
                padding-right: 0;
            }
        }
    }
}

