.ps-deal-of-day {
    .ps-section__header {
        .ps-block--countdown-deal {
            h3 {
                margin-right: 0;
                margin-left: 70px;
                text-align: right;
            }

            figure {
                figcaption {
                    margin-right: 0;
                    margin-left: 20px;
                }
            }
        }
    }
}

.ps-layout--shop {
