.ps-product__content {
    text-align: right;

    .ps-product__title {
        text-align: right;
    }

    .ps-product__vendor {
        text-align: right !important;
    }

    .ps-product__rating {
        display: flex;
        flex-flow: row-reverse nowrap;
        justify-content: flex-end;
        align-items: start;
    }
}

.ps-product--search-result {
    .ps-product__content {
        text-align: right;
    }
}

.ps-block--site-features {
    .ps-block__right {
        padding-left: 0;
        padding-right: 20px;
    }

    .ps-block__item {
        &:first-child {
            border-right: 0;
        }
    }
}

.owl-carousel {
    &.owl-rtl {

    }
}

.ps-block--download-app {
    .ps-block__content {
        padding-right: 30px !important;
        text-align: right;
    }

    input {
        border: 1px solid #ccc;
        border-radius: 4px;
    }

    button {
        margin-right: 4px;
        border-radius: 4px;
    }
}

.ps-block--shop-features {
    .ps-carousel__next {
        i {
            &:before {
                content: "\e93b" !important;
            }
        }

    }

    .ps-carousel__prev {
        i {
            &:before {
                content: "\e93c" !important;
            }
        }
    }
}
